import opg from 'ts/opg.ts';
import Panel from 'ts/ui/Panel.ts';
import Table from 'ts/ui/Table.ts';
import {Combo} from 'ts/ui/Combo';
import {Languages} from 'ts/util/Languages';
import {Cache, store} from '../../../ts/util/store';
import {url} from '../../../ts/util/utils';

let cache = Cache.getInstance();

console.log('Languages:', Languages);

opg.api({
	'list!post': 'Information/QueryInformations',
	setStick: 'Information/SetInformationStick',
	cancelStick: 'Information/CancelInformationStick',
	'setStatus!post': 'Information/EnableOrDisableInformationById',
});

const lpg = Languages.package;

const infoPage = __uri('/pages/content/sponsor/add.html');
const moduleName = lpg.sponsor;

let panel: Panel = opg.wrapPanel('#tbSearch', {
	title: `${moduleName}${lpg.search}`,
	btnSearchText: `<i class="ico-find"></i> ${lpg.search}`,
	btnSearchClick: function () {
		let param = $('#tbSearch').fieldsToJson();

		if (param.beginDate && param.beginDate.indexOf(' ') < 0) {
			param.beginDate += ' 00:00:00';
		}
		if (param.endDate && param.endDate.indexOf(' ') < 0) {
			param.endDate += ' 23:59:59';
		}
		//console.log(panel.jq, param);
		tb.update(param);
	},
});




let tb: Table = opg('#tb').table({
	api: opg.api.list,
	param: {informationType: 2},
	titleBar: {
		title: `${lpg.list}`,
	},
	columns: [
		{
			text: 'ID', width: 120,
			src: 'id',
		},
		{
			text: lpg.barcode ,
			src: 'title',
		},
		{
			text: lpg.company,
			src: 'status',
			width: 100,
			render: v => v ? lpg.online : lpg.offline,
		},
	],
	pagination: true,
});




